.header {
  border: 1px solid #989393;
  box-sizing: border-box;
}

.headerContainer {
  max-width: 1172px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: auto;
}

.linksContainer {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.item {
  color: #ffffff;
  margin-right: 52px;
  font-family: "Museo Sans Cyrl", sans-serif;
  font-weight: 500;
  font-size: 16px;
}

.item:hover {
  color: #b1b1b1;
  cursor: pointer;
  text-underline-offset: 12px;
  text-decoration: underline;
  text-decoration-color: #b1b1b1;
  transition: all 0.3s ease;
}

.item:last-of-type {
  margin-right: 0;
}

.burgerButton {
  width: 30px;
  height: 30px;
  position: relative;
  z-index: 2;
  overflow: hidden;
  display: none;
}

.burgerButton:hover {
  cursor: pointer;
  opacity: 0.7;
}

.burgerButtonWrapper {
  width: 30px;
  height: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  transition: all 0.5s;
}

.burgerButtonWrapper:nth-of-type(2) {
  top: calc(50% - 5px);
}
.burgerButtonWrapper:nth-of-type(3) {
  top: calc(50% + 5px);
}

@media screen and (max-width: 1279px) {
  .headerContainer {
    max-width: 1000px;
    justify-content: space-around;
  }

  .item {
    margin-right: 15px;
  }
}

@media screen and (max-width: 1023px) {
  .headerContainer {
    max-width: 700px;
    justify-content: space-between;
  }

  .item {
    margin-right: 10px;
    font-size: 12px;
  }
}

@media screen and (max-width: 767px) {
  .header {
    border: none;
  }

  .headerContainer {
    align-items: center;
    padding: 10px;
    box-sizing: border-box;
  }

  .linksContainer {
    display: none;
  }

  .item {
    margin-right: 0;
    margin-bottom: 6px;
  }

  .item:hover {
    text-underline-offset: 6px;
  }

  .burgerButton {
    display: block;
  }
}
